import React, { useState } from 'react'
import MainNavbar from '../../../ui/MainNavbar'
import LayoutHeading from '../../../ui/LayoutHeading'
import { Button, Col, Container, Form, Row } from "react-bootstrap";
import { Link, useNavigate } from "react-router-dom";
import * as userAction from '../../../../redux/users/user.Actions'
import * as userReducer from '../../../../redux/users/user.Reducer'
import { AppDispatch, RootState, useAppDispatch } from "../../../../redux/store";
import { useSelector } from 'react-redux';
import Spinner from '../../../../extraUtil/Spinner';
import * as ToastUtil from '../../../../util/ToastUtil'


interface IState {
    password: string;
    confirmPassword: string;
};

const ChangePassword: React.FC = () => {

    const [validated, setValidated] = useState(false);
    const dispatch: AppDispatch = useAppDispatch();
    const navigate = useNavigate();

    const [user, setUser] = useState<IState>({
        password: "",
        confirmPassword: ""
    });

    const updateInput = (event: React.ChangeEvent<HTMLInputElement | any>) => {
        setUser({
            ...user,
            [event.target.name]: event.target.value
        })
    };

    // get users data from store
    const userReduxState: userReducer.InitialState = useSelector((state: RootState) => {
        return state[userReducer.userFeatureKey];
    });

    const { loading } = userReduxState;

    const handleSubmit = (event: React.FormEvent | any) => {
        event.preventDefault();
        const form = event.currentTarget;
        if (form.checkValidity() === true) {
            if (user.password === user.confirmPassword) {
                dispatch(userAction.changePasswordAction({ password: user.password })).then((response: any) => {
                    if (response && !response.error) {
                        navigate("/users/profile");
                    }
                });
            } else {
                ToastUtil.displayErrorMessage("Both the passwords are not matched");
            }
        }
        if (form.checkValidity() === false) {
            event.preventDefault();
            event.stopPropagation();
        }
        setValidated(true);
    };

    return (
        <>
            <MainNavbar />

            {
                loading && <Spinner />
            }

            <LayoutHeading icon='bi bi-file-earmark-lock2-fill' heading={' Change Password'} />

            <Container className="mt-3">
                <Row>
                    <Col xs={4}>
                        <Form noValidate validated={validated} onSubmit={handleSubmit}>
                            <Form.Group className="mb-2">
                                <Form.Control
                                    required
                                    pattern={"^(?!.*\\s)(?=.*[A-Z])(?=.*[a-z])(?=.*[0-9])(?=.*[~`!@#$%^&*]).{6,15}$"}
                                    name={'password'}
                                    value={user.password}
                                    onChange={e => updateInput(e)}
                                    type={'password'} placeholder={'Password'}></Form.Control>
                                <Form.Control.Feedback type="invalid">
                                    Please choose a Strong Password.
                                </Form.Control.Feedback>
                            </Form.Group>
                            <Form.Group className="mb-2">
                                <Form.Control
                                    required
                                    pattern={"^(?!.*\\s)(?=.*[A-Z])(?=.*[a-z])(?=.*[0-9])(?=.*[~`!@#$%^&*]).{6,15}$"}
                                    name={'confirmPassword'}
                                    value={user.confirmPassword}
                                    onChange={e => updateInput(e)}
                                    type={'password'} placeholder={'Confirm Password'}></Form.Control>
                                <Form.Control.Feedback type="invalid">
                                    Please choose a Strong Password.
                                </Form.Control.Feedback>
                            </Form.Group>
                            <Form.Group className="mb-2">
                                <Button type={'submit'} variant={'success'}>Update Password</Button>
                                <Link to={'/'} className="btn btn-dark ms-2">Cancel</Link>
                            </Form.Group>
                        </Form>
                    </Col>
                </Row>
            </Container>
        </>
    )
}

export default ChangePassword