import React, { useEffect } from 'react';
import MainNavbar from '../../../ui/MainNavbar';
import LayoutHeading from '../../../ui/LayoutHeading';
import { Badge, Col, Container, ListGroup, ListGroupItem, Row, Table } from "react-bootstrap";
import { AppDispatch, RootState, useAppDispatch } from '../../../../redux/store';
import * as orderReducer from '../../../../redux/orders/orders.Reducer'
import * as orderActions from '../../../../redux/orders/order.Actions'
import { useSelector } from 'react-redux';
import Spinner from '../../../../extraUtil/Spinner';
import { Link } from 'react-router-dom';
import NotFound from '../../../../extraUtil/NotFound';


const MyOrders: React.FC = () => {

    const dispatch: AppDispatch = useAppDispatch();

    // get order from Redux
    const orderReduxState: orderReducer.InitialState = useSelector((state: RootState) => {
        return state[orderReducer.orderFeatureKey];
    });

    const { loading, orders } = orderReduxState;

    useEffect(() => {
        dispatch(orderActions.getMyOrdersAction());
    }, []);

    return (
        <>

            {
                loading && <Spinner />
            }
            <MainNavbar />

            {/* <pre>{JSON.stringify(orders)}</pre> */}

            <LayoutHeading icon='bi bi-bag-heart-fill' heading={'My Orders'} />

            {
                orders && orders.length > 0 ? <>
                    <Container>
                        <Row>
                            <Col>
                                <Table striped hover className="text-center shadow-lg">
                                    <thead className="bg-success text-white">
                                        <tr>
                                            <th>Order Number</th>
                                            <th>Order Details</th>
                                            <th>Order Placed On</th>
                                            <th>Order By</th>
                                            <th>Total Amount</th>
                                            <th>Payment Type</th>
                                            <th>Order Status</th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        {
                                            orders.map(order => {
                                                return (
                                                    <tr key={order._id}>
                                                        <td>{order._id}</td>
                                                        <td>
                                                            <ListGroup>
                                                                {
                                                                    order.products.map(item => {
                                                                        return (
                                                                            <ListGroupItem key={item._id}>
                                                                                <Row>
                                                                                    <Col xs={2}>
                                                                                        <img src={item?.imageUrl}
                                                                                            alt="" width={25} height={25} />
                                                                                    </Col>
                                                                                    <Col xs={8}>
                                                                                        <Link
                                                                                            className="text-decoration-none text-success"
                                                                                            to={`/products/view/Fashion/${item._id}`}>{item.title}</Link>
                                                                                    </Col>
                                                                                </Row>
                                                                            </ListGroupItem>
                                                                        )
                                                                    })
                                                                }
                                                            </ListGroup>
                                                        </td>
                                                        <td>{new Date(order.createdAt).toLocaleDateString()}
                                                            {new Date(order.createdAt).toLocaleTimeString()}
                                                        </td>
                                                        <td>{order.orderBy?.username}</td>
                                                        <td>&#8377; {Number(order.grandTotal).toFixed(2)}</td>
                                                        <td>{order.paymentType}</td>
                                                        <td className="fw-bold">
                                                            {
                                                                order.orderStatus === "Order Placed" &&
                                                                <Badge bg={'info'}>{order.orderStatus}</Badge>
                                                            }
                                                            {
                                                                order.orderStatus === "Processing" &&
                                                                <Badge bg={'primary'}>{order.orderStatus}</Badge>
                                                            }
                                                            {
                                                                order.orderStatus === "Dispatched" &&
                                                                <Badge bg={'success'}>{order.orderStatus}</Badge>
                                                            }
                                                            {
                                                                order.orderStatus === "Delivered" &&
                                                                <Badge bg={'warning'}>{order.orderStatus}</Badge>
                                                            }
                                                            {
                                                                order.orderStatus === "Cancelled" &&
                                                                <Badge bg={'danger'}>{order.orderStatus}</Badge>
                                                            }
                                                            {
                                                                order.orderStatus === "Completed" &&
                                                                <Badge bg={'success'}>{order.orderStatus}</Badge>
                                                            }
                                                        </td>
                                                    </tr>
                                                )
                                            })
                                        }
                                    </tbody>
                                </Table>
                            </Col>
                        </Row>
                    </Container>
                </> : <>
                    <NotFound />
                </>
            }
        </>
    )
};
export default MyOrders;