import axios from "axios";
import { IOrderRequest } from "../model/IOrderRequest";
import { IOrderResponse } from "../model/IOrderResponse";


const serverUrl = process.env.REACT_APP_EXPRESS_SERVER_URL ? process.env.REACT_APP_EXPRESS_SERVER_URL : "";

/**
* @usage : place an order
* @url : http://localhost:9000/api/orders/place
* @param : products[{product, count,price}],total,tax,grandTotal,paymentType
* @method : POST
* @access : PRIVATE
*/
export const placeOrder = (order: IOrderRequest): Promise<{ data: IOrderResponse, msg: string }> => {
    const dataUrl: string = `${serverUrl}/api/orders/place`;
    return axios.post(dataUrl, order);
};

/**
* @usage : get all orders
* @url : http://localhost:9000/api/orders/all
* @param : no-params
* @method : GET
* @access : PRIVATE
*/
export const getAllOrders = (): Promise<{ data: IOrderResponse[] }> => {
    const dataUrl: string = `${serverUrl}/api/orders/all`;
    return axios.get(dataUrl);
};

/**
* @usage : get my orders
* @url : http://localhost:9000/api/orders/me
* @param : no-params
* @method : GET
* @access : PRIVATE
*/
export const getMyOrders = (): Promise<{ data: IOrderResponse[] }> => {
    const dataUrl: string = `${serverUrl}/api/orders/me`;
    return axios.get(dataUrl);
};

/**
* @usage : update order status
* @url : http://localhost:9000/api/orders/:orderId
* @param : orderStatus
* @method : POST
* @access : PRIVATE
*/
export const updateOrderStatus = (orderId: string, orderStatus: string): Promise<{ data: IOrderResponse, msg: string }> => {
    const dataUrl: string = `${serverUrl}/api/orders/${orderId}`;
    return axios.post(dataUrl, { orderStatus });
};