import React from 'react';
import { Link } from 'react-router-dom';
import * as tokenUtil from '../../util/TokenUtil'

const Home: React.FC = () => {
    return (
        <>
            {/* <Navbar/> */}
            <div className="landing-page">
                <div className="wrapper">
                    <div className="d-flex flex-column justify-content-center align-items-center text-center h-100">
                        <p className="h3 display-1 text-primary main-heading">React E-Commerce</p>
                        <div>
                            {
                                tokenUtil.isLoggedIn() ?
                                    <Link to={'/products/fashion'} className="btn btn-success">Start Shopping</Link>
                                    :
                                    <Link to={'/users/login'} className="btn btn-warning ms-2">
                                        <i className="bi bi-lock-fill"></i>Login
                                    </Link>
                            }
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
};
export default Home;