import React, { useEffect, useState } from 'react';
import MainNavbar from '../../../../ui/MainNavbar';
import LayoutHeading from '../../../../ui/LayoutHeading';
import { Button, Card, Col, Container, Row } from "react-bootstrap";
import { Link } from "react-router-dom";
import * as categoryActions from '../../../../../redux/categories/categories.Actions'
import * as categoryReducer from '../../../../../redux/categories/categories.Reducer'
import * as productActions from '../../../../../redux/products/product.Actions'
import * as productReducer from '../../../../../redux/products/product.Reducer'
import { AppDispatch, RootState, useAppDispatch } from '../../../../../redux/store';
import { useSelector } from 'react-redux';
import Spinner from '../../../../../extraUtil/Spinner';
import { ICategory } from '../../../../categories/model/ICategory';
import { IProductResponse } from '../../../model/IProductResponse';
import NotFound from '../../../../../extraUtil/NotFound';
import { ISubCategory } from '../../../../categories/model/ISubCategory';
import ProductSideBar from '../../../ProductSidebar';
import { addToCartAction } from '../../../../../redux/carts/cart.Reducer';

const HouseHoldCatalogue: React.FC = () => {

    const dispatch: AppDispatch = useAppDispatch();

    const [category, setCategory] = useState<ICategory>({} as ICategory);

    const [products, setProducts] = useState<IProductResponse[]>([] as IProductResponse[]);

    const [subCategories, setSubCategories] = useState<ISubCategory[]>([] as ISubCategory[]); // filter the product
    const [filteredProducts, setFilteredProducts] = useState<IProductResponse[]>([] as IProductResponse[]);//
    const [filterLoading, setFilterLoading] = useState<boolean>(false);//

    // get all categories from redux
    const categoryReduxState: categoryReducer.InitialState = useSelector((state: RootState) => {
        return state[categoryReducer.categoryFeatureKey];
    });

    // get all products from redux
    const productReduxState: productReducer.InitialState = useSelector((state: RootState) => {
        return state[productReducer.productFeatureKey];
    })

    const { loading: categoryLoading, categories: categoryRedux } = categoryReduxState;
    const { loading: productLoading, products: productsRedux } = productReduxState

    useEffect(() => {
        dispatch(categoryActions.getAllCategoriesAction());
    }, []);

    // to find the specific category name
    useEffect(() => {
        if (categoryRedux.length > 0) {
            const category: ICategory | undefined = categoryRedux.find(item => item.name.toLowerCase().trim().includes("household"));
            if (category) {
                setCategory(category); //
                if (category.subCategories) { // set checked to filter products
                    setSubCategories(category.subCategories.map(sub => {
                        return {
                            ...sub,
                            isChecked: true
                        }
                    }));
                };
            };
        }
    }, [categoryRedux]);

    // get products base on category id 
    useEffect(() => {
        if (category && Object.keys(category).length > 0) {
            dispatch(productActions.getAllProductsWithCategoryIdAction({ categoryId: category._id }))
        }
    }, [category]);

    // set products to state 
    useEffect(() => {
        if (productsRedux && productsRedux.length > 0) {
            setProducts(productsRedux);
            setFilteredProducts(productsRedux)  //
        }
    }, [productsRedux]);

    // to filter the products
    const filterTheProducts = (subCategories: ISubCategory[]) => {
        let subs = subCategories.map(item => {
            if (item.isChecked) {
                return item._id;
            }
        }).filter(item => item !== undefined);
        setFilterLoading(true);
        setTimeout(() => {
            setFilteredProducts(products.filter(item => subs.includes(item?.subCategoryObj?._id)));
            setFilterLoading(false);
        }, 500);
    };

    const clickaddToCart = (product: IProductResponse) => {
        if (product) {
            dispatch({
                type: `${addToCartAction}`,
                payload: {
                    product: product,
                    count: 1
                }
            })
        }
    };

    return (
        <>
            {
                (categoryLoading || productLoading || filterLoading) && <Spinner />
            }
            <MainNavbar />

            {/* <pre>{JSON.stringify(category)}</pre> */}
            {/* <pre>{JSON.stringify(products)}</pre> */}

            <Container fluid>
                <Row>
                    <Col xs={1}>
                        <ProductSideBar subCategories={subCategories} setSubCategories={setSubCategories}
                            filteredTheProducts={filterTheProducts} />
                    </Col>
                    <Col className="product-layout">
                        <LayoutHeading icon='bi bi-house-heart-fill' heading={'Household Catalogue'} />
                    </Col>
                </Row>
            </Container>

            {
                filteredProducts && filteredProducts.length > 0 &&
                <Container>
                    <Row>
                        {
                            filteredProducts.map((product) => {
                                return (
                                    <Col xs={3} className="mb-3 text-center " key={product._id}>
                                        <Card >
                                            <Card.Header className="fashion-products">
                                                <Link to={`/products/view/${category.name.toLowerCase()}/${product._id}`}>
                                                    <img src={product.imageUrl} alt="" width={230}
                                                        className="text-center m-auto d-block" height={170}
                                                    />
                                                </Link>
                                            </Card.Header>
                                            <Card.Body>
                                                <small className="fw-bold text-success">{product.title}
                                                </small>
                                                <br />
                                                <small
                                                    className="fw-bold text-danger">&#8377; {Number(product.price).toFixed(2)}
                                                </small>
                                                <br />
                                                <Button variant={'warning'} size={'sm'}
                                                    onClick={() => clickaddToCart(product)}>Add to Cart
                                                </Button>
                                            </Card.Body>
                                        </Card>
                                    </Col>
                                )
                            })
                        }
                    </Row>
                </Container>
            }
            {
                filteredProducts.length === 0 &&
                <NotFound />
            }
        </>
    )

};
export default HouseHoldCatalogue;