import { createSlice, isRejectedWithValue, SerializedError } from "@reduxjs/toolkit";
import * as categoryActions from './categories.Actions';
import * as ToastUtil from '../../util/ToastUtil'
import { ICategory } from "../../module/categories/model/ICategory";
import { ISubCategory } from "../../module/categories/model/ISubCategory";


export const categoryFeatureKey = "categoryrFeature";

export interface InitialState {
    loading: boolean;
    errorMessage: SerializedError;
    category: ICategory;
    categories: ICategory[];
    subCategory: ISubCategory;
};

const initialState: InitialState = {
    loading: false,
    errorMessage: {} as SerializedError,
    category: {} as ICategory,
    categories: [] as ICategory[],
    subCategory: {} as ISubCategory
};

export const categorySlice = createSlice({
    name: "categorySlice",
    initialState: initialState,
    reducers: {},
    extraReducers: (builder) => {

        // create category 

        builder.addCase(categoryActions.createCategoryAction.pending, (state, action) => {
            // set to state 
            state.loading = true;
        }).addCase(categoryActions.createCategoryAction.fulfilled, (state, action) => {
            // set to state 
            state.loading = false;
            state.category = action.payload.data;
            // show toast message 
            ToastUtil.displaySuccessMessage(action.payload.msg);
        }).addCase(categoryActions.createCategoryAction.rejected, (state, action) => {
            // set to state 
            state.loading = false;
            if (isRejectedWithValue(action)) {
                state.errorMessage = action.payload
                // show toast message 
                ToastUtil.displayErrorMessage("Create Category Failed");
            };
        });


        // create subCategory 

        builder.addCase(categoryActions.createSubCategoryAction.pending, (state, action) => {
            // set to state 
            state.loading = true;
        }).addCase(categoryActions.createSubCategoryAction.fulfilled, (state, action) => {
            state.loading = false;
            state.subCategory = action.payload.data;
            ToastUtil.displaySuccessMessage(action.payload.msg);
        }).addCase(categoryActions.createSubCategoryAction.rejected, (state, action) => {
            state.loading = false;
            if (isRejectedWithValue(action)) {
                state.errorMessage = action.payload;
                ToastUtil.displayErrorMessage("Create SubCategory Failed");
            };
        });


        // get All Category 

        builder.addCase(categoryActions.getAllCategoriesAction.pending, (state, action) => {
            // set to state 
            state.loading = true;
        }).addCase(categoryActions.getAllCategoriesAction.fulfilled, (state, action) => {
            state.loading = false;
            state.categories = action.payload.data;
        }).addCase(categoryActions.getAllCategoriesAction.rejected, (state, action) => {
            state.loading = false;
            if (isRejectedWithValue(action)) {
                state.errorMessage = action.payload;
                ToastUtil.displayErrorMessage("Get All Categories Failed");
            };
        });
    }
});