import React, { useEffect } from 'react';
import MainNavbar from '../../../ui/MainNavbar';
import LayoutHeading from '../../../ui/LayoutHeading';
import { Button, Card, Col, Container, ListGroup, ListGroupItem, Row, Table } from "react-bootstrap";
import * as cartReducer from '../../../../redux/carts/cart.Reducer'
import * as cartReduxHelper from '../../../../redux/carts/cartReduxHelper'
import { useSelector } from 'react-redux';
import { AppDispatch, RootState, useAppDispatch } from '../../../../redux/store';
import Spinner from '../../../../extraUtil/Spinner';
import NotFound from '../../../../extraUtil/NotFound';
import { decrementQuantityAction, deleteCartItemAction, incrementQuantityAction } from '../../../../redux/carts/cart.Reducer';
import * as cartActions from '../../../../redux/carts/cart.Actions'
import { useNavigate } from 'react-router-dom';
import { ICartRequest } from '../../model/ICartRequest';


const CartPage: React.FC = () => {

    const dispatch: AppDispatch = useAppDispatch();

    const navigate = useNavigate()

    // get data from redux state 
    const cartReduxState: cartReducer.InitialState = useSelector((state: RootState) => {
        return state[cartReducer.cartFeatureKey];
    });

    const { cart, loading: cartLoading } = cartReduxState;

    const incrementQty = (productId: string) => {
        if (productId) {
            dispatch({
                type: `${incrementQuantityAction}`,
                payload: {
                    productId: productId
                }
            })
        };
    };

    const decrementQty = (productId: string | undefined) => {
        if (productId) {
            dispatch({
                type: `${decrementQuantityAction}`,
                payload: {
                    productId: productId
                }
            })
        };
    };

    const deleteCartItem = (productId: string | undefined) => {
        if (productId) {
            dispatch({
                type: `${deleteCartItemAction}`,
                payload: {
                    productId: productId
                }
            })
        };
    };

    const clickCheckOut = () => {

        //this is because we send data to backend like that why we are using this
        const cartPayload: ICartRequest = {
            products: cart.products.map(product => {
                return {
                    productObj: product._id.toString(),
                    count: product.count.toString(),
                    price: product.price.toString()
                };
            }),
            tax: cartReduxHelper.calculateTax(cart.products).toString(),
            total: cartReduxHelper.calculateTotal(cart.products).toString(),
            grandTotal: cartReduxHelper.calculateGrandTotal(cart.products).toString()
        };

        dispatch(cartActions.createCartAction({ cart: cartPayload })).then((response: any) => {
            if (response && !response.error) {
                navigate("/carts/checkout");
            }
        })
    };

    useEffect(() => {
        // if (tokenUtil.isLoggedIn()) {
        // dispatch(userActions.getUserDataAction());
        dispatch(cartActions.getCartInfoAction())
        // }
    }, []);

    return (
        <>

            {
                cartLoading && <Spinner />
            }
            <MainNavbar />
            <LayoutHeading icon='bi bi-cart' heading={'Your Cart'} />

            {/* <pre>{JSON.stringify(cart)}</pre> */}

            {
                cart && cart.products.length > 0 &&

                <Container className="mt-3">
                    <Row>
                        <Col xs={9}>
                            <Card className="shadow-lg">
                                <Card.Header className="bg-success text-white">
                                    <p className="h4">Cart Items</p>
                                </Card.Header>
                                <Card.Body className="bg-light-grey">
                                    <Table striped hover className="text-center">
                                        <thead className='bg-warning'>
                                            <tr>
                                                <th>SNO</th>
                                                <th>Image</th>
                                                <th>Title</th>
                                                <th>Price</th>
                                                <th>Qty</th>
                                                <th>Total</th>
                                                <th>Actions</th>
                                            </tr>
                                        </thead>
                                        <tbody>

                                            {
                                                cart && cart.products.map((product, index) => {
                                                    return (
                                                        <tr key={product._id}>
                                                            <td>{index + 1}</td>
                                                            <td>
                                                                <img src={product.imageUrl} alt="" width={50}
                                                                    height={50} />
                                                            </td>
                                                            <td>{product.title}</td>
                                                            <td>&#8377;{product.price}</td>
                                                            <td>
                                                                <i
                                                                    className="bi bi-dash-circle-fill me-1"
                                                                    onClick={() => decrementQty(product._id)}>
                                                                </i>
                                                                {product.count}
                                                                <i className="bi bi-plus-circle-fill ms-1"
                                                                    onClick={() => incrementQty(product._id)}>
                                                                </i>
                                                            </td>
                                                            <td>&#8377;{Number(product.price) * product.count}</td>
                                                            <td>
                                                                <Button variant={'danger'}>
                                                                    <i className="bi bi-trash-fill"
                                                                        onClick={() => deleteCartItem(product._id)}>
                                                                    </i>
                                                                </Button>
                                                            </td>
                                                        </tr>
                                                    )
                                                })
                                            }

                                        </tbody>
                                    </Table>
                                </Card.Body>
                            </Card>
                        </Col>
                        <Col xs={3}>
                            <Card className="shadow-lg">
                                <Card.Header className="bg-success text-white">
                                    <p className="h4">Cart Total : </p>
                                </Card.Header>
                                <Card.Body className="bg-light-grey">
                                    <ListGroup>
                                        <ListGroupItem>Total
                                            : <span
                                                className="fw-bold">
                                                &#8377; {cartReduxHelper.calculateTotal(cart.products)}
                                            </span>
                                        </ListGroupItem>
                                        <ListGroupItem>Tax :
                                            <span className="fw-bold">
                                                &#8377; {cartReduxHelper.calculateTax(cart.products).toFixed(2)}
                                            </span>
                                        </ListGroupItem>
                                        <ListGroupItem>Grand Total :
                                            <span className="fw-bold">
                                                &#8377; {cartReduxHelper.calculateGrandTotal(cart.products).toFixed(2)}
                                            </span>
                                        </ListGroupItem>
                                    </ListGroup>
                                    <div className="d-grid gap-2 mt-3">
                                        <Button variant='warning' onClick={clickCheckOut}>Checkout</Button>
                                    </div>
                                </Card.Body>
                            </Card>
                        </Col>
                    </Row>
                </Container >
            }
            {
                cart && cart.products.length === 0 && <NotFound />
            }
        </>
    )
};
export default CartPage;