import React, { useState } from 'react'
import MainNavbar from '../../../ui/MainNavbar'
import LayoutHeading from '../../../ui/LayoutHeading'
import { Button, Col, Container, Form, Row } from "react-bootstrap";
import { Link, useNavigate } from 'react-router-dom';
import { IAddress } from '../../../addresses/model/IAddresses';
import * as userAction from '../../../../redux/users/user.Actions'
import * as userReducer from '../../../../redux/users/user.Reducer'
import { AppDispatch, RootState, useAppDispatch } from "../../../../redux/store";
import { useSelector } from 'react-redux';
import Spinner from '../../../../extraUtil/Spinner';


const AddShippingAddress: React.FC = () => {

    const [validated, setValidated] = useState(false);
    const dispatch: AppDispatch = useAppDispatch();
    const navigate = useNavigate();

    const [address, setAddress] = useState<IAddress>({
        mobile: "",
        flat: "",
        landmark: "",
        street: "",
        city: "",
        state: "",
        country: "",
        pinCode: ""
    });

    const updateInput = (event: React.ChangeEvent<HTMLInputElement | any>) => {
        setAddress({
            ...address,
            [event.target.name]: event.target.value
        })
    };

    // get users data from store
    const userReduxState: userReducer.InitialState = useSelector((state: RootState) => {
        return state[userReducer.userFeatureKey];
    });

    const { loading } = userReduxState;

    const handleSubmit = (event: React.FormEvent | any) => {
        event.preventDefault();
        const form = event.currentTarget;
        if (form.checkValidity() === true) {
            dispatch(userAction.createNewAddressAction({ address: address })).then((response: any) => {
                if (response && !response.error) {
                    navigate("/users/profile");
                }
            })
        }
        if (form.checkValidity() === false) {
            event.preventDefault();
            event.stopPropagation();
        }
        setValidated(true);
    };

    return (
        <>
            <MainNavbar />

            {
                loading && <Spinner />
            }

            {/* <pre>{JSON.stringify(address)}</pre> */}
            <LayoutHeading heading='Add Shipping Address' />
            <Container className="mt-3">
                <Row>
                    <Col xs={4}>
                        <Form noValidate validated={validated} onSubmit={handleSubmit}>
                            <Form.Group className="mb-2">
                                <Form.Control
                                    required
                                    value={address.mobile}
                                    name={'mobile'}
                                    onChange={e => updateInput(e)}
                                    type={'number'} placeholder={'Mobile'}></Form.Control>
                            </Form.Group>
                            <Form.Group className="mb-2">
                                <Form.Control
                                    required
                                    value={address.flat}
                                    name={'flat'}
                                    onChange={e => updateInput(e)}
                                    type={'text'} placeholder={'Flat'}></Form.Control>
                            </Form.Group>
                            <Form.Group className="mb-2">
                                <Form.Control
                                    required
                                    value={address.landmark}
                                    name={'landmark'}
                                    onChange={e => updateInput(e)}
                                    type={'text'} placeholder={'Landmark'}></Form.Control>
                            </Form.Group>
                            <Form.Group className="mb-2">
                                <Form.Control
                                    required
                                    value={address.street}
                                    name={'street'}
                                    onChange={e => updateInput(e)}
                                    type={'text'} placeholder={'Street'}></Form.Control>
                            </Form.Group>
                            <Form.Group className="mb-2">
                                <Form.Control
                                    required
                                    value={address.city}
                                    name={'city'}
                                    onChange={e => updateInput(e)}
                                    type={'text'} placeholder={'City'}></Form.Control>
                            </Form.Group>
                            <Form.Group className="mb-2">
                                <Form.Control
                                    required
                                    value={address.state}
                                    name={'state'}
                                    onChange={e => updateInput(e)}
                                    type={'text'} placeholder={'State'}></Form.Control>
                            </Form.Group>
                            <Form.Group className="mb-2">
                                <Form.Control
                                    required
                                    value={address.country}
                                    name={'country'}
                                    onChange={e => updateInput(e)}
                                    type={'text'} placeholder={'Country'}></Form.Control>
                            </Form.Group>
                            <Form.Group className="mb-2">
                                <Form.Control
                                    required
                                    value={address.pinCode}
                                    name={'pinCode'}
                                    onChange={e => updateInput(e)}
                                    type={'number'} placeholder={'PinCode'}></Form.Control>
                            </Form.Group>
                            <Form.Group className="mb-2">
                                <Button type={'submit'} variant={'success'}>Create</Button>
                                <Link to={'/users/profile'} className="btn btn-dark ms-2">Cancel</Link>
                            </Form.Group>
                        </Form>
                    </Col>
                </Row>
            </Container>
        </>
    )
}

export default AddShippingAddress