import { createAsyncThunk } from "@reduxjs/toolkit";
import * as CartService from '../../module/carts/services/CartService'
import * as AuthUtil from '../../util/AuthUtil'
import { ICartRequest } from "../../module/carts/model/ICartRequest";
import { ICartResponse } from "../../module/carts/model/ICartResponse";


export const createCartAction: any = createAsyncThunk("users/createCartAction",
    async (payload: { cart: ICartRequest }, { rejectWithValue }): Promise<{ data: ICartResponse, msg: string } | any> => {
        try {
            if (AuthUtil.isSetTokenToHeader()) {
                const { cart } = payload;
                const response = await CartService.createCart(cart);
                return response.data;
            };
        } catch (error: any) {
            if (!error.response) {
                throw error
            };
            return rejectWithValue(error)
        };
    });

export const getCartInfoAction: any = createAsyncThunk("users/getCartInfoAction",
    async (payload: {}, { rejectWithValue }): Promise<{ data: ICartResponse | null } | any> => {
        try {
            if (AuthUtil.isSetTokenToHeader()) {
                const response = await CartService.getCartInfo();
                return response.data;
            };
        } catch (error: any) {
            if (!error.response) {
                throw error
            };
            return rejectWithValue(error)
        };
    });