import React from 'react';
import noImg from '../assets/img/noimg.png'

const NotFound: React.FC = () => {
    return (
        <>
            <div className="container">
                <div className="row">
                    <div className="col text-center">
                        <img src={noImg} alt="Not Found" height={350} />
                    </div>
                </div>
            </div>
        </>
    );
}
export default NotFound;
