import { createSlice, isRejectedWithValue, SerializedError } from "@reduxjs/toolkit";
import * as orderActions from './order.Actions'
import * as ToastUtil from '../../util/ToastUtil'
import { IOrderStorageType } from './../../module/orders/model/IOrderResponse';
import * as OrderReduxHelper from './OrderReduxHelper'


export const orderFeatureKey = "orderFeature";

export interface InitialState {
    loading: boolean;
    errorMessage: SerializedError;
    // order: IOrderResponse;
    // orders: IOrderResponse[];
    order: IOrderStorageType;
    orders: IOrderStorageType[];
};

const initialState: InitialState = {
    loading: false,
    errorMessage: {} as SerializedError,
    // order: {} as IOrderResponse,
    // orders: [] as IOrderResponse[]
    order: {} as IOrderStorageType,
    orders: [] as IOrderStorageType[]
};

export const orderSlice = createSlice({
    name: "orderSlice",
    initialState: initialState,
    reducers: {},
    extraReducers: (builder) => {

        // place order

        builder.addCase(orderActions.placeOrderAction.pending, (state, action) => {
            state.loading = true;
        }).addCase(orderActions.placeOrderAction.fulfilled, (state, action) => {
            state.loading = false;
            // state.order = action.payload.data;
            if (action.payload.data) {
                state.order = OrderReduxHelper.convertFromServerToClientOrder(action.payload.data);
            }
            ToastUtil.displaySuccessMessage(action.payload.msg);
        }).addCase(orderActions.placeOrderAction.rejected, (state, action) => {
            state.loading = false;
            if (isRejectedWithValue(action)) {
                state.errorMessage = action.payload;
                ToastUtil.displayErrorMessage("Place Order Failed");
            };
        });


        // get all orders

        builder.addCase(orderActions.getAllOrdersAction.pending, (state, action) => {
            state.loading = true;
        }).addCase(orderActions.getAllOrdersAction.fulfilled, (state, action) => {
            state.loading = false;
            // state.orders = action.payload.data;
            if (action.payload.data) {
                state.orders = OrderReduxHelper.convertFromServerToClientOrders(action.payload.data);
            }
        }).addCase(orderActions.getAllOrdersAction.rejected, (state, action) => {
            state.loading = false;
            if (isRejectedWithValue(action)) {
                state.errorMessage = action.payload;
                ToastUtil.displayErrorMessage("Get All Orders Failed");
            };
        });


        // get my orders

        builder.addCase(orderActions.getMyOrdersAction.pending, (state, action) => {
            state.loading = true;
        }).addCase(orderActions.getMyOrdersAction.fulfilled, (state, action) => {
            state.loading = false;
            // state.orders = action.payload.data;
            if (action.payload.data) {
                state.orders = OrderReduxHelper.convertFromServerToClientOrders(action.payload.data);
            }
        }).addCase(orderActions.getMyOrdersAction.rejected, (state, action) => {
            state.loading = false;
            if (isRejectedWithValue(action)) {
                state.errorMessage = action.payload;
                ToastUtil.displayErrorMessage("Get My Order Failed");
            };
        });


        // update order status

        builder.addCase(orderActions.updateOrderStatusAction.pending, (state, action) => {
            state.loading = true;
        }).addCase(orderActions.updateOrderStatusAction.fulfilled, (state, action) => {
            state.loading = false;
            // state.order = action.payload.data;
            if (action.payload.data) {
                state.order = OrderReduxHelper.convertFromServerToClientOrder(action.payload.data);
            }
            ToastUtil.displaySuccessMessage(action.payload.msg);
        }).addCase(orderActions.updateOrderStatusAction.rejected, (state, action) => {
            state.loading = false;
            if (isRejectedWithValue(action)) {
                state.errorMessage = action.payload;
                ToastUtil.displayErrorMessage("Update Order Status Failed");
            };
        });
    }
});
