import { IOrderProduct, IOrderResponse, IOrderResponseProduct, IOrderStorageType } from "../../module/orders/model/IOrderResponse";


// filtering the objects from [] 
export const convertOrderProducts = (products: IOrderResponseProduct[]): IOrderProduct[] => {
    return products.map((product) => {
        if (product.productObj) {
            return {
                _id: product?.productObj?._id,
                title: product?.productObj?.title,
                count: product?.count,
                description: product?.productObj?.description,
                imageUrl: product?.productObj?.imageUrl,
                brand: product?.productObj?.brand,
                price: product?.price,
                quantity: product?.productObj?.quantity,
                sold: product?.productObj?.sold,
                userObj: product?.productObj?.userObj,
                categoryObj: product?.productObj?.userObj,
                subCategoryObj: product?.productObj?.subCategoryObj,
                createdAt: product?.productObj?.createdAt,
                updatedAt: product?.productObj?.updatedAt
            }
        } else {
            return {} as IOrderProduct;
        }
    });
};

// for single order
export const convertFromServerToClientOrder = (order: IOrderResponse): IOrderStorageType => {
    const products: IOrderProduct[] = order.products.map((product) => {
        return {
            _id: product?.productObj?._id,
            title: product?.productObj?.title,
            count: product?.count,
            description: product?.productObj?.description,
            imageUrl: product?.productObj?.imageUrl,
            brand: product?.productObj?.brand,
            price: product?.price,
            quantity: product?.productObj?.quantity,
            sold: product?.productObj?.sold,
            userObj: product?.productObj?.userObj,
            categoryObj: product?.productObj?.userObj,
            subCategoryObj: product?.productObj?.subCategoryObj,
            createdAt: product?.productObj?.createdAt,
            updatedAt: product?.productObj?.updatedAt
        }
    });
    return {
        ...order,
        products: products
    } as IOrderStorageType;
};

// for multiple orders 
export const convertFromServerToClientOrders = (orders: IOrderResponse[]): IOrderStorageType[] => {
    return orders.map(serverOrder => {
        return {
            _id: serverOrder._id,
            products: convertOrderProducts(serverOrder.products),
            total: serverOrder.total,
            tax: serverOrder.tax,
            grandTotal: serverOrder.grandTotal,
            paymentType: serverOrder.paymentType,
            orderStatus: serverOrder.orderStatus,
            orderBy: serverOrder.orderBy,
            createdAt: serverOrder.createdAt,
            updatedAt: serverOrder.updatedAt,
            __v: serverOrder.__v
        }
    })
};

export const getOrderStatusList = ()=>{
    return [
        "Order Placed",
        "Processing",
        "Dispatched",
        "Delivered",
        "Cancelled",
        "Completed"
    ]
};