import axios from "axios";
import { ICategory } from "../model/ICategory";
import { ISubCategory } from "../model/ISubCategory";

const serverUrl = process.env.REACT_APP_EXPRESS_SERVER_URL ? process.env.REACT_APP_EXPRESS_SERVER_URL : "";

/**
* @usage : Create a Category
* @url : http://localhost:9000/api/categories/
* @body : name, description
* @method : POST
* @access : PRIVATE
*/
export const createCategory = (category: ICategory): Promise<{ data: ICategory, msg: string }> => {
    const dataUrl: string = `${serverUrl}/api/categories/`;
    return axios.post(dataUrl, category);
};

/**
* @usage : Create a Sub Category
* @url : http://localhost:9000/api/categories/:categoryId
* @body : name, description
* @method : POST
* @access : PRIVATE
*/
export const createSubCategory = (categoryId: string, subCategory: ICategory): Promise<{ data: ISubCategory, msg: string }> => {
    const dataUrl: string = `${serverUrl}/api/categories/${categoryId}`;
    return axios.post(dataUrl, subCategory);
};

/**
* @usage : Get all categories
* @url : http://localhost:9000/api/categories/
* @body : no-params
* @method : GET
* @access : PRIVATE
*/
export const getAllCategories = (): Promise<{ data: ICategory[] }> => {
    const dataUrl: string = `${serverUrl}/api/categories/`;
    return axios.get(dataUrl);
};