import axios from "axios";
import { IAddress } from "../model/IAddresses";

// const serverUrl = "http://localhost:9000";
const serverUrl = process.env.REACT_APP_EXPRESS_SERVER_URL ? process.env.REACT_APP_EXPRESS_SERVER_URL : "";

/**
 * @usage : Create New Address
 * @url : http://localhost:9000/api/addresses/new
 * @body : mobile,flat,landmark,street,city,state,country,pinCode
 * @method : POST
 * @access : PRIVATE
 */
export const createNewAddress = (address: IAddress): Promise<{ data: IAddress, msg: string }> => {
    const dataUrl: string = `${serverUrl}/api/addresses/new`;
    return axios.post(dataUrl, address);
};

/**
 * @usage : Update Address
 * @url : http://localhost:9000/api/addresses/:addressId
 * @body : mobile,address,landmark,street,city,state,country,pinCode
 * @method : PUT
 * @access : PRIVATE
 */
export const updateAddress = (address: IAddress, addressId: string): Promise<{ data: IAddress, msg: string }> => {
    const dataUrl: string = `${serverUrl}/api/addresses/${addressId}`;
    return axios.put(dataUrl, address);
};

/**
* @usage : Get Address
* @url : http://localhost:9000/api/addresses/me
* @body : no-params
* @method : GET
* @access : PRIVATE
*/
export const getAddress = (): Promise<{ data: IAddress | {} }> => {
    const dataUrl: string = `${serverUrl}/api/addresses/me`;
    return axios.get(dataUrl);
};

/**
* @usage : Delete Address
* @url : http://localhost:9000/api/addresses/:addressId
* @body : no-params
* @method : DELETE
* @access : PRIVATE
*/
export const deleteAddress = (addressId: string): Promise<{ data: {}, msg: string }> => {
    const dataUrl: string = `${serverUrl}/api/addresses/${addressId}`;
    return axios.delete(dataUrl);
};