import { createAsyncThunk } from "@reduxjs/toolkit";
import * as CategoryService from '../../module/categories/services/CategoryService'
import { ICategory } from "../../module/categories/model/ICategory";
import { ISubCategory } from "../../module/categories/model/ISubCategory";
import * as AuthUtil from '../../util/AuthUtil'


export const createCategoryAction: any = createAsyncThunk("category/createCategoryAction",
    async (payload: { category: ICategory }, { rejectWithValue }): Promise<{ data: ICategory, msg: string } | any> => {
        try {
            if (AuthUtil.isSetTokenToHeader()) {
                const { category } = payload;
                const response = await CategoryService.createCategory(category);
                return response.data;
            };
        } catch (error: any) {
            if (!error.response) {
                throw error
            };
            return rejectWithValue(error)
        };
    });

export const createSubCategoryAction: any = createAsyncThunk("category/createSubCategoryAction",
    async (payload: { categoryId: string, subCategory: ICategory }, { rejectWithValue }): Promise<{ data: ISubCategory, msg: string } | any> => {
        try {
            if (AuthUtil.isSetTokenToHeader()) {
                const { categoryId, subCategory } = payload;
                const response = await CategoryService.createSubCategory(categoryId, subCategory);
                return response.data;
            };
        } catch (error: any) {
            if (!error.response) {
                throw error
            };
            return rejectWithValue(error)
        };
    });

export const getAllCategoriesAction: any = createAsyncThunk("category/getAllCategoriesAction",
    async (payload: {}, { rejectWithValue }): Promise<{ data: ICategory[] } | any> => {
        try {
            if (AuthUtil.isSetTokenToHeader()) {
                const response = await CategoryService.getAllCategories();
                return response.data;
            };
        } catch (error: any) {
            if (!error.response) {
                throw error
            };
            return rejectWithValue(error)
        };
    });