import React, { useEffect } from 'react';
import MainNavbar from '../../../ui/MainNavbar';
import LayoutHeading from '../../../ui/LayoutHeading';
import { Button, Col, Container, ListGroup, ListGroupItem, Row } from "react-bootstrap";
import { Link, useParams } from 'react-router-dom';
import * as productActions from '../../../../redux/products/product.Actions'
import * as productReducer from '../../../../redux/products/product.Reducer'
import { AppDispatch, RootState, useAppDispatch } from '../../../../redux/store';
import { useSelector } from 'react-redux';
import Spinner from '../../../../extraUtil/Spinner';
import NotFound from '../../../../extraUtil/NotFound';
import { IProductResponse } from '../../model/IProductResponse';
import { addToCartAction } from '../../../../redux/carts/cart.Reducer';


const ViewProduct: React.FC = () => {

    const { productId, categoryName } = useParams();

    const dispatch: AppDispatch = useAppDispatch();

    // get product data from Redux
    const productReduxState: productReducer.InitialState = useSelector((state: RootState) => {
        return state[productReducer.productFeatureKey];
    });

    const { loading, product } = productReduxState;

    // if change in product get product  
    useEffect(() => {
        if (productId) {
            dispatch(productActions.getProductAction({ productId: productId }))
        }
    }, [productId]);

    const clickaddToCart = (product: IProductResponse) => {
        if (product) {
            dispatch({
                type: `${addToCartAction}`,
                payload: {
                    product: product,
                    count: 1
                }
            })
        }
    };


    return (
        <>
            {
                loading && <Spinner />
            }
            <MainNavbar />

            {/* <pre>{JSON.stringify(product)}</pre> */}

            <LayoutHeading icon='bi bi-eye-fill' heading={'View Product'} />
            {
                product && Object.keys(product).length > 0 &&
                <Container className="mt-3">
                    <Row>
                        <Col xs={4}>
                            <img src={product.imageUrl} alt="" className="img-fluid rounded-3 shadow-lg w-75" />
                            <div>
                                <Link to={`/products/${categoryName?.toLowerCase()}`} className="btn btn-warning mt-3">
                                    <i className='bi bi-arrow-left-circle-fill'></i> Back
                                </Link>
                            </div>
                        </Col>
                        <Col xs={6}>
                            <ListGroup>
                                <ListGroupItem>Name :
                                    <span className="fw-bold">{product.title}</span>
                                </ListGroupItem>
                                <ListGroupItem>Brand :
                                    <span className="fw-bold">{product.brand}</span>
                                </ListGroupItem>
                                <ListGroupItem>Category : <span
                                    className="fw-bold">{product.categoryObj?.name}</span>
                                </ListGroupItem>
                                <ListGroupItem>Sub Category : <span
                                    className="fw-bold">{product.subCategoryObj?.name}</span>
                                </ListGroupItem>
                                <ListGroupItem>Brand :
                                    <span className="fw-bold">{product.brand}</span>
                                </ListGroupItem>
                                <ListGroupItem>Price : <span
                                    className="fw-bold">&#8377; {product.price}</span>
                                </ListGroupItem>
                                <ListGroupItem>Description : <span>{product.description}</span>
                                </ListGroupItem>
                            </ListGroup>
                            <Button variant={'warning'} className="mt-2"
                                onClick={() => clickaddToCart(product)}>Add to Cart
                            </Button>
                        </Col>
                    </Row>
                </Container>
            }
            {
                product && Object.keys(product).length === 0 &&
                <NotFound />
            }
        </>
    )
};
export default ViewProduct;