import React, { useEffect } from 'react';
import MainNavbar from '../../../ui/MainNavbar';
import LayoutHeading from '../../../ui/LayoutHeading';
import { Col, Container, Row, Card, Button, ListGroup, ListGroupItem } from "react-bootstrap";
import { Link, useNavigate } from 'react-router-dom';
import { AppDispatch, RootState, useAppDispatch } from '../../../../redux/store';
import * as userAction from '../../../../redux/users/user.Actions'
import * as userReducer from '../../../../redux/users/user.Reducer'
import * as cartAction from '../../../../redux/carts/cart.Actions'
import * as cartReducer from '../../../../redux/carts/cart.Reducer'
import { useSelector } from 'react-redux';
import Spinner from '../../../../extraUtil/Spinner';
import * as cartReduxHelper from '../../../../redux/carts/cartReduxHelper'
import * as orderActions from '../../../../redux/orders/order.Actions'
import { IOrderRequest } from '../../../orders/model/IOrderRequest';
import { clearCartAction } from '../../../../redux/carts/cart.Reducer';

const CheckOutPage: React.FC = () => {

    const dispatch: AppDispatch = useAppDispatch();
    const navigate = useNavigate();

    // get address info from state 

    const userReduxState: userReducer.InitialState = useSelector((state: RootState) => {
        return state[userReducer.userFeatureKey]
    });

    // get cart info from state 

    const cartReduxState: cartReducer.InitialState = useSelector((state: RootState) => {
        return state[cartReducer.cartFeatureKey]
    });

    const { loading: userLoading, address } = userReduxState;
    const { loading: cartLoading, cart } = cartReduxState;

    useEffect(() => {
        dispatch(userAction.getAddressAction());
        dispatch(cartAction.getCartInfoAction());
    }, []);

    const clickPlaceOrder = () => {

        //this is because we send data to backend like that why we are using this
        const order: IOrderRequest = {
            products: cart.products.map(product => {
                return {
                    productObj: product._id,
                    count: product.count.toString(),
                    price: product.price,
                    paymentType: "COD"
                };
            }),
            tax: cartReduxHelper.calculateTax(cart.products).toString(),
            total: cartReduxHelper.calculateTotal(cart.products).toString(),
            grandTotal: cartReduxHelper.calculateGrandTotal(cart.products).toString(),
            paymentType: "COD"
        } as IOrderRequest

        dispatch(orderActions.placeOrderAction({ order: order })).then((response: any) => {
            if (response && !response.error) {
                navigate("/orders/placed");
                dispatch({
                    type: `${clearCartAction}`
                })
            }
        })
    };

    return (
        <>
            {
                (userLoading || cartLoading) && <Spinner />
            }

            <MainNavbar />

            {/* <pre>{JSON.stringify(address)}</pre> */}
            {/* <pre>{JSON.stringify(cart)}</pre> */}

            <LayoutHeading icon='bi bi-cart-check-fill' heading={'CheckOut Page'} />

            <Container className="mt-3">
                <Row>
                    {
                        address && Object.keys(address).length > 0 &&
                        <Col xs={8}>
                            <Card className="shadow-lg">
                                <Card.Header className="bg-success text-white d-flex justify-content-between">
                                    <h4>Shipping Address</h4>
                                    <Link to={`/users/edit-shipping-address/${address._id}?router=checkout`}>
                                        <Button variant={'primary'}>
                                            <i className="bi bi-pencil"></i>
                                        </Button>
                                    </Link>
                                </Card.Header>
                                <Card.Body className="bg-light-grey">
                                    <ListGroup>
                                        <ListGroupItem>
                                            Name : <span className="fw-bold">{address.name}</span>
                                        </ListGroupItem>
                                        <ListGroupItem>
                                            Mobile : <span className="fw-bold">{address.mobile}</span>
                                        </ListGroupItem>
                                        <ListGroupItem>
                                            Email : <span className="fw-bold">{address.email}</span>
                                        </ListGroupItem>
                                        <ListGroupItem>
                                            Flat : <span className="fw-bold">{address.flat}</span>
                                        </ListGroupItem>
                                        <ListGroupItem>
                                            Street : <span className="fw-bold">{address.street}</span>
                                        </ListGroupItem>
                                        <ListGroupItem>
                                            Landmark : <span className="fw-bold">{address.landmark}</span>
                                        </ListGroupItem>
                                        <ListGroupItem>
                                            City : <span className="fw-bold">{address.city}</span>
                                        </ListGroupItem>
                                        <ListGroupItem>
                                            State : <span className="fw-bold">{address.state}</span>
                                        </ListGroupItem>
                                        <ListGroupItem>
                                            Country : <span className="fw-bold">{address.country}</span>
                                        </ListGroupItem>
                                        <ListGroupItem>
                                            Pincode : <span className="fw-bold">{address.pinCode}</span>
                                        </ListGroupItem>

                                    </ListGroup>
                                </Card.Body>
                            </Card>
                        </Col>
                    }
                    {
                        cart && Object.keys(cart).length > 0 &&
                        <Col xs={4}>
                            <Card className="shadow-lg">
                                <Card.Header className="bg-success text-white">
                                    <h4>Your Cart</h4>
                                </Card.Header>
                                <Card.Body className="bg-light-grey">
                                    <ListGroup>
                                        {
                                            cart.products && cart.products.map((item, index) => {
                                                return (
                                                    <ListGroupItem key={item._id} className='text-primary'>
                                                        <Row>
                                                            <Col xs={3} >
                                                                <img src={item.imageUrl} alt="" width={100}
                                                                    height={100} className="img-fluid"
                                                                />
                                                            </Col>
                                                            <Col xs={9}>
                                                                <small><b>{item.title}</b></small>
                                                                <br />
                                                                <small>count : <b>{item.count}</b></small>
                                                                <br />
                                                                <small>price
                                                                    : &#8377; <b>{Number(item.price).toFixed(2)}</b>
                                                                </small>
                                                            </Col>
                                                        </Row>
                                                    </ListGroupItem>
                                                )
                                            })
                                        }
                                    </ListGroup>
                                    <ListGroup className="mt-2">
                                        <ListGroupItem>Total
                                            : <span
                                                className="fw-bold">
                                                &#8377; {cartReduxHelper.calculateTotal(cart.products).toFixed(2)}
                                            </span>
                                        </ListGroupItem>
                                        <ListGroupItem>Tax :
                                            <span className="fw-bold">
                                                &#8377; {cartReduxHelper.calculateTax(cart.products).toFixed(2)}
                                            </span>
                                        </ListGroupItem>
                                        <ListGroupItem>Grand Total :
                                            <span className="fw-bold">
                                                &#8377; {cartReduxHelper.calculateGrandTotal(cart.products).toFixed(2)}
                                            </span>
                                        </ListGroupItem>
                                    </ListGroup>

                                    <div className="d-grid">
                                        <Button variant={'warning'} className="mt-2" onClick={clickPlaceOrder}>
                                            Place Order
                                        </Button>
                                    </div>
                                </Card.Body>
                            </Card>
                        </Col>
                    }
                </Row>
            </Container>
        </>
    )
};
export default CheckOutPage;