import Container from 'react-bootstrap/Container';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import NavDropdown from 'react-bootstrap/NavDropdown';
import { useSelector } from 'react-redux';
import { Link, useNavigate } from 'react-router-dom';
import * as userReducer from '../../redux/users/user.Reducer'
import { AppDispatch, RootState, useAppDispatch } from '../../redux/store';
import * as cartReducer from '../../redux/carts/cart.Reducer'
import * as cartActions from '../../redux/carts/cart.Actions'
import Spinner from '../../extraUtil/Spinner';
import { useEffect } from 'react';
import * as tokenUtil from '../../util/TokenUtil'
import logo from '../../assets/img/logo.png'


function MainNavbar() {

    const navigate = useNavigate();

    const dispatch: AppDispatch = useAppDispatch();

    // get data from redux state 
    const userReduxState: userReducer.InitialState = useSelector((state: RootState) => {
        return state[userReducer.userFeatureKey];
    });

    // get data from redux state 
    const cartReduxState: cartReducer.InitialState = useSelector((state: RootState) => {
        return state[cartReducer.cartFeatureKey];
    });

    const { user, loading: userLoading } = userReduxState;
    const { cart, loading: cartLoading } = cartReduxState;

    const logOff = () => {
        dispatch({
            type: `${userReducer.logOffAction}`
        });
        navigate('/');
    };

    useEffect(() => {
        if (tokenUtil.isLoggedIn()) {
            dispatch(cartActions.getCartInfoAction())
        }
    }, []);

    return (
        <>
            {
                (userLoading || cartLoading) && <Spinner />
            }

            <Navbar bg="success" expand="lg" variant='dark'>

                {/* <pre>{JSON.stringify(user)}</pre> */}

                <Container>
                    <Navbar.Brand >
                        <Link to={'/'} className='text-white text-decoration-none'>
                            <i><img src={logo} alt="" width={60} /></i>
                            React E-Commerce
                        </Link>
                    </Navbar.Brand>
                    <Navbar.Toggle aria-controls="basic-navbar-nav" />
                    <Navbar.Collapse id="basic-navbar-nav">
                        {
                            user && Object.keys(user).length > 0 && <>
                                <Nav className="">
                                    <Link to={'/products/fashion'} className="nav-link">Fashion</Link>
                                </Nav>
                                <Nav className="">
                                    <Link to={'/products/electronics'} className="nav-link">Electronics</Link>
                                </Nav>
                                <Nav className="">
                                    <Link to={'/products/household'} className="nav-link">Household</Link>
                                </Nav>
                                {
                                    user.isAdmin && <Nav>
                                        <NavDropdown title="Admin" id="basic-nav-dropdown">
                                            <NavDropdown.Item
                                                onClick={() => navigate("/categories/add")}>Add Sub-Categories
                                            </NavDropdown.Item>
                                            <NavDropdown.Item
                                                onClick={() => navigate("/products/upload")}>Upload Products
                                            </NavDropdown.Item>
                                            <NavDropdown.Divider />
                                            <NavDropdown.Item
                                                onClick={() => navigate("/products/admin")}>Manage Products
                                            </NavDropdown.Item>
                                            <NavDropdown.Item
                                                onClick={() => navigate("/orders/admin")}>Manage Orders
                                            </NavDropdown.Item>
                                        </NavDropdown>
                                    </Nav>
                                }

                            </>
                        }

                        <div className="d-flex ms-auto">
                            {
                                user && Object.keys(user).length > 0 && <>
                                    <Nav className="">
                                        {
                                            cart && <Link to={'/carts/list'} className="nav-link pe-3">
                                                <i className="bi bi-cart-fill"></i>
                                                <span className="cart-count">{cart.products.length}</span>
                                            </Link>
                                        }
                                    </Nav>

                                    <Nav className="">
                                        <Link to={'/users/profile'} className="nav-link">
                                            <img src={user.imageUrl} alt="" className="rounded-circle"
                                                width={25} height={25}
                                            />
                                        </Link>
                                    </Nav>

                                    <Nav>
                                        <NavDropdown title={user.username} id="basic-nav-dropdown">
                                            <NavDropdown.Item
                                                onClick={() => navigate("/users/profile")}>Profile
                                            </NavDropdown.Item>
                                            <NavDropdown.Item
                                                onClick={() => navigate("/users/change-password")}>Change Password
                                            </NavDropdown.Item>
                                            <NavDropdown.Item
                                                onClick={() => navigate("/users/orders/me")}>My Orders
                                            </NavDropdown.Item>
                                            <NavDropdown.Divider />
                                            <NavDropdown.Item onClick={logOff} >
                                                <i className="bi bi-power"></i> LogOut</NavDropdown.Item>
                                        </NavDropdown>
                                    </Nav>
                                </>
                            }
                        </div>
                    </Navbar.Collapse>
                </Container>
            </Navbar>
        </>
    );
}

export default MainNavbar;