import React, { useEffect } from 'react';
import { Button, Col, Container, ListGroup, ListGroupItem, Row } from "react-bootstrap";
import MainNavbar from '../../../ui/MainNavbar';
import LayoutHeading from '../../../ui/LayoutHeading';
import * as orderReducer from '../../../../redux/orders/orders.Reducer'
import * as orderActions from '../../../../redux/orders/order.Actions'
import { useSelector } from 'react-redux';
import { AppDispatch, RootState, useAppDispatch } from '../../../../redux/store';
import { Link } from 'react-router-dom';
import Spinner from '../../../../extraUtil/Spinner';
import orderImg from '../../../../assets/img/order-success.png'

const OrderDetails: React.FC = () => {

    const dispatch: AppDispatch = useAppDispatch();

    // get order from Redux
    const orderReduxState: orderReducer.InitialState = useSelector((state: RootState) => {
        return state[orderReducer.orderFeatureKey];
    });

    const { loading, order } = orderReduxState;

    useEffect(() => {
        dispatch(orderActions.getMyOrdersAction());
    }, []);

    return (
        <>
            {loading && <Spinner />}

            <MainNavbar />

            {/* <pre>{JSON.stringify(order)}</pre> */}

            <LayoutHeading icon='bi bi-bag-heart-fill' heading={'My Orders'} />

            {
                order && Object.keys(order).length > 0 &&
                <Container>
                    <Row>
                        <Col xs={3}>
                            <img src={orderImg} alt="" className="rounded-circle img-fluid" />
                        </Col>
                        <Col xs={8}>
                            <ListGroup>
                                <ListGroupItem className="fw-bold text-success">
                                    Order is placed Successfully
                                </ListGroupItem>
                                <ListGroupItem>
                                    Order Number : <span className="fw-bold">{order._id.toUpperCase()}</span>
                                </ListGroupItem>
                                <ListGroupItem>
                                    Total Amount : <span
                                        className="fw-bold">&#8377; {Number(order.grandTotal).toFixed(2)}</span>
                                </ListGroupItem>
                                <ListGroupItem>
                                    Order Status : <span className="fw-bold">{order.orderStatus}</span>
                                </ListGroupItem>
                                <ListGroupItem>
                                    Payment mode : <span className="fw-bold">{order.paymentType}</span>
                                </ListGroupItem>
                            </ListGroup>

                            <Link to={'/products/fashion'}>
                                <Button variant={'warning'} className="mt-3">Continue Shopping</Button>
                            </Link>
                        </Col>
                    </Row>
                </Container>
            }
        </>
    )
};
export default OrderDetails;