import React, { useEffect, useRef, useState } from 'react';
import LayoutHeading from '../../../ui/LayoutHeading';
import MainNavbar from '../../../ui/MainNavbar';
import { useNavigate, useParams } from "react-router-dom";
import * as productActions from '../../../../redux/products/product.Actions';
import * as productReducer from '../../../../redux/products/product.Reducer';
import { AppDispatch, RootState, useAppDispatch } from "../../../../redux/store";
import { useSelector } from "react-redux";
import * as categoryActions from "../../../../redux/categories/categories.Actions";
import * as categoryReducer from "../../../../redux/categories/categories.Reducer";
import { Button, Col, Container, Form, Row } from "react-bootstrap";
import { IProductRequest } from '../../model/IProductRequest';
import { ISubCategory } from '../../../categories/model/ISubCategory';
import * as UploadImageWidget from '../../../../util/ImageUploadWeight'
import * as ToastUtil from '../../../../util/ToastUtil'
import { ICategory } from '../../../categories/model/ICategory';
import Spinner from '../../../../extraUtil/Spinner';

const EditProduct: React.FC = () => {

    const dispatch: AppDispatch = useAppDispatch();
    const { productId } = useParams();
    const cloudinaryRef = useRef<any>();
    const widgetRef = useRef<any>();
    const navigate = useNavigate();

    const [validated, setValidated] = useState(false);
    const [product, setProduct] = useState<IProductRequest>({
        title: "",
        imageUrl: "",
        categoryId: "",
        subCategoryId: "",
        brand: "",
        price: "",
        quantity: "",
        description: ""
    });
    const [subCategories, setSubCategories] = useState<ISubCategory[]>([] as ISubCategory[]);
    const [categoryName, setCategoryName] = useState<string>("");


    // get products state from redux
    const productReduxState: productReducer.InitialState = useSelector((state: RootState) => {
        return state[productReducer.productFeatureKey];
    });

    // get all categories from redux
    const categoryReduxState: categoryReducer.InitialState = useSelector((state: RootState) => {
        return state[categoryReducer.categoryFeatureKey];
    });

    const { loading: categoryLoading, categories } = categoryReduxState;

    const { loading: productLoading, product: productRedux } = productReduxState;

    useEffect(() => {
        dispatch(categoryActions.getAllCategoriesAction());
    }, []);

    useEffect(() => {
        if (productId) {
            dispatch(productActions.getProductAction({ productId: productId }));
        }
    }, [productId]);

    useEffect(() => {
        UploadImageWidget.upload(cloudinaryRef, widgetRef).then((imageUrl) => {
            if (imageUrl) {
                populateDataBack(imageUrl.toString());
            }
        }).catch((error) => {
            ToastUtil.displayErrorMessage(error);
        });
    }, [cloudinaryRef, widgetRef]);

    useEffect(() => {
        if (productRedux && Object.keys(productRedux).length > 0) {
            setProduct({
                title: productRedux.title,
                imageUrl: productRedux.imageUrl,
                categoryId: productRedux.categoryObj?._id,
                subCategoryId: productRedux.subCategoryObj?._id,
                brand: productRedux.brand,
                price: productRedux.price,
                quantity: productRedux.quantity,
                description: productRedux.description
            })
        }
    }, [productRedux])

    const populateDataBack = (imageUrl: string) => {
        alert("setting back" + imageUrl)
        if (productRedux && Object.keys(productRedux).length > 0) {
            setProduct({
                title: productRedux.title,
                imageUrl: imageUrl,
                categoryId: productRedux.categoryObj?._id,
                subCategoryId: productRedux.subCategoryObj?._id,
                brand: productRedux.brand,
                price: productRedux.price,
                quantity: productRedux.quantity,
                description: productRedux.description
            })
        }
    };

    useEffect(() => {
        if (product.categoryId) {
            onSelectCategory(product.categoryId);
        }
    }, [product.categoryId])

    const onSelectCategory = (theCategoryId: string) => {
        setProduct({ ...product, categoryId: theCategoryId, subCategoryId: "" });
        const category: ICategory | undefined = categories.find(category => category._id === theCategoryId);
        if (category && category.subCategories) {
            setSubCategories(category.subCategories);
            setCategoryName(category.name);
        } else {
            setSubCategories([] as ISubCategory[]);
            setCategoryName("");
        }
    };

    const onSelectSubCategory = (e: React.ChangeEvent | any) => {
        setProduct({ ...product, subCategoryId: e.target.value });
    };

    const updateInput = (e: React.ChangeEvent | any) => {
        setProduct(
            {
                ...product,
                [e.target.name]: e.target.value
            }
        );
    };

    const clickUploadImage = () => {
        widgetRef.current.open();
    };

    const handleSubmit = (event: React.FormEvent | any) => {
        event.preventDefault();
        const form = event.currentTarget;
        if (form.checkValidity() === true) {
            if (productId && product && Object.keys(product).length > 0) {
                dispatch(productActions.updateProductAction({
                    product: product,
                    productId: productId
                })).then((response: any) => {
                    if (response && !response.error) {
                        navigate(`/products/admin`);
                    }
                });
            }
        }
        if (form.checkValidity() === false) {
            event.preventDefault();
            event.stopPropagation();
        }
        setValidated(true);
    };


    return (
        <>
            {(categoryLoading || productLoading) && <Spinner />}
            <MainNavbar />
            <LayoutHeading heading={'Edit Product'} />
            {
                categories && categories.length > 0 &&
                <Container className="mt-3">
                    <Row>
                        <Col xs={4}>
                            <Button variant={'warning'} onClick={clickUploadImage}>Upload Image</Button>
                            <Form className="mt-3" noValidate validated={validated} onSubmit={handleSubmit}>
                                <Form.Group className="mb-2">
                                    <Form.Select
                                        value={product.categoryId}
                                        name={'categoryId'}
                                        onChange={(e) => onSelectCategory(e.target.value)}>
                                        <option value="">Select a Category</option>
                                        {
                                            categories.map(category => {
                                                return (<option key={category._id}
                                                    value={category._id}>{category.name}</option>)
                                            })
                                        }
                                    </Form.Select>
                                </Form.Group>
                                <Form.Group className="mb-2">
                                    <Form.Select
                                        value={product.subCategoryId}
                                        onChange={(e) => onSelectSubCategory(e)}>
                                        <option value="">Select a Sub Category</option>
                                        {
                                            subCategories.map(subCategory => {
                                                return (<option key={subCategory._id}
                                                    value={subCategory._id}>{subCategory.name}</option>)
                                            })
                                        }
                                    </Form.Select>
                                </Form.Group>
                                <Form.Group className="mb-2">
                                    <Form.Control
                                        required
                                        value={product.title}
                                        name={'title'}
                                        onChange={e => updateInput(e)}
                                        type={'text'} placeholder={'Title'}></Form.Control>
                                </Form.Group>
                                <Form.Group className="mb-2">
                                    <Form.Control
                                        required
                                        value={product.brand}
                                        name={'brand'}
                                        onChange={e => updateInput(e)}
                                        type={'text'} placeholder={'Brand'}></Form.Control>
                                </Form.Group>
                                <Form.Group className="mb-2">
                                    <Form.Control
                                        required
                                        value={product.price}
                                        name={'price'}
                                        onChange={e => updateInput(e)}
                                        type={'number'} placeholder={'Price'}></Form.Control>
                                </Form.Group>
                                <Form.Group className="mb-2">
                                    <Form.Control
                                        required
                                        value={product.quantity}
                                        name={'quantity'}
                                        onChange={e => updateInput(e)}
                                        type={'number'} placeholder={'Quantity'}></Form.Control>
                                </Form.Group>
                                <Form.Group className="mb-2">
                                    <Form.Control
                                        required
                                        value={product.description}
                                        onChange={e => updateInput(e)}
                                        name={'description'}
                                        as="textarea" rows={3} placeholder={'Description'}></Form.Control>
                                </Form.Group>
                                <Form.Group className="mb-2">
                                    <Button variant={'success'} type={'submit'}>Upload Product</Button>
                                </Form.Group>
                            </Form>
                        </Col>
                        <Col xs={3}>
                            <img src={product.imageUrl} alt="" className="img-fluid shadow-lg rounded-3" />
                        </Col>
                    </Row>
                </Container>
            }
        </>
    )
};
export default EditProduct;