import React, { useEffect, useRef, useState } from 'react';
import MainNavbar from '../../../ui/MainNavbar';
import LayoutHeading from '../../../ui/LayoutHeading';
import { Button, Col, Container, Form, Row } from "react-bootstrap";
import { IProductRequest } from '../../model/IProductRequest';
import * as ImageUploadWidget from '../../../../util/ImageUploadWeight'
import * as ToastUtil from '../../../../util/ToastUtil'
import { ISubCategory } from '../../../categories/model/ISubCategory';
import * as categoryReducer from '../../../../redux/categories/categories.Reducer'
import * as categoryActions from '../../../../redux/categories/categories.Actions'
import * as productActinos from '../../../../redux/products/product.Actions'
import { useSelector } from 'react-redux';
import { AppDispatch, RootState, useAppDispatch } from '../../../../redux/store';
import Spinner from '../../../../extraUtil/Spinner';
import { ICategory } from '../../../categories/model/ICategory';
import { Link, useNavigate } from 'react-router-dom';


const UploadProducts: React.FC = () => {

    const dispatch: AppDispatch = useAppDispatch();

    const navigate = useNavigate();

    const cloudinaryRef = useRef<any>();
    const widgetRef = useRef<any>();

    const [validated, setValidated] = useState(false);

    const [product, setProduct] = useState<IProductRequest>({
        imageUrl: "",
        categoryId: "",
        subCategoryId: "",
        title: "",
        brand: "",
        price: "",
        quantity: "",
        description: ""
    });

    const [categoryName, setCategoryName] = useState<string>(""); // to navigate the selected category
    const [subCategories, setSubCategories] = useState<ISubCategory[]>([] as ISubCategory[]);

    const updateInput = (e: React.ChangeEvent | any) => {
        setProduct(
            {
                ...product,
                [e.target.name]: e.target.value
            }
        );
    };

    // get all categories from redux
    const categoryReduxState: categoryReducer.InitialState = useSelector((state: RootState) => {
        return state[categoryReducer.categoryFeatureKey];
    });

    const { loading, categories } = categoryReduxState;

    useEffect(() => {
        dispatch(categoryActions.getAllCategoriesAction());
    }, []);

    // upload image
    useEffect(() => {
        ImageUploadWidget.upload(cloudinaryRef, widgetRef).then((imageUrl) => {
            if (imageUrl) {
                setProduct({ ...product, imageUrl: imageUrl.toString() }) // set imgUrl to state
            }
        }).catch((error) => {
            ToastUtil.displayErrorMessage(error);
        });
    }, []);

    const clickUploadImage = () => {
        widgetRef.current.open();
    };

    const onSelectCategory = (e: React.ChangeEvent | any) => {
        setProduct({ ...product, categoryId: e.target.value, subCategoryId: "" });
        const category: ICategory | undefined = categories.find(category => category._id === e.target.value);
        if (category && category.subCategories) {
            setSubCategories(category.subCategories);  // set sub category state from category
            setCategoryName(category.name); // to navigate the selected category
        } else {
            setSubCategories([] as ISubCategory[]); // to make empty state
            setCategoryName(""); // to navigate the selected category to empty state
        }
    };

    const onSelectSubCategory = (e: React.ChangeEvent | any) => {
        setProduct({ ...product, subCategoryId: e.target.value });
    };

    const handleSubmit = (event: React.FormEvent | any) => {
        event.preventDefault();
        const form = event.currentTarget;
        if (form.checkValidity() === true) {
            dispatch(productActinos.createProductAction({ product: product })).then((response: any) => {
                if (response && !response.error) {
                    navigate(`/products/${categoryName.toLowerCase()}`);
                }
            })
        }
        if (form.checkValidity() === false) {
            event.preventDefault();
            event.stopPropagation();
        }
    };

    return (
        <>
            {
                loading && <Spinner />
            }
            <MainNavbar />

            {/* <pre>{JSON.stringify(product)}</pre> */}
            {/* <pre>{JSON.stringify(categories)}</pre> */}

            <LayoutHeading icon='bi bi-file-plus' heading={'Upload Product'} />

            <Container className="mt-3">
                <Row>
                    <Col xs={4}>
                        <Button variant={'warning'} onClick={clickUploadImage}>Upload Image</Button>
                        <Form className="mt-3" noValidate validated={validated} onSubmit={handleSubmit}>
                            <Form.Group className="mb-2">
                                <Form.Select onChange={e => onSelectCategory(e)}>
                                    <option value="">Select a Category</option>
                                    {
                                        categories.map(category => {
                                            return (
                                                <option
                                                    value={category._id} key={category._id}>{category.name}
                                                </option>
                                            )
                                        })
                                    }
                                </Form.Select>
                            </Form.Group>
                            <Form.Group className="mb-2">
                                <Form.Select onChange={e => onSelectSubCategory(e)}>
                                    <option value="">Select a Sub Category</option>
                                    {
                                        subCategories.map(subCategory => {
                                            return (
                                                <option
                                                    value={subCategory._id} key={subCategory._id}>{subCategory.name}
                                                </option>
                                            )
                                        })
                                    }
                                </Form.Select>
                            </Form.Group>
                            <Form.Group className="mb-2">
                                <Form.Control
                                    required
                                    value={product.title}
                                    name={'title'}
                                    onChange={e => updateInput(e)}
                                    type={'text'} placeholder={'Title'}></Form.Control>
                            </Form.Group>
                            <Form.Group className="mb-2">
                                <Form.Control
                                    required
                                    value={product.brand}
                                    name={'brand'}
                                    onChange={e => updateInput(e)}
                                    type={'text'} placeholder={'Brand'}></Form.Control>
                            </Form.Group>
                            <Form.Group className="mb-2">
                                <Form.Control
                                    required
                                    value={product.price}
                                    name={'price'}
                                    onChange={e => updateInput(e)}
                                    type={'number'} placeholder={'Price'}></Form.Control>
                            </Form.Group>
                            <Form.Group className="mb-2">
                                <Form.Control
                                    required
                                    value={product.quantity}
                                    name={'quantity'}
                                    onChange={e => updateInput(e)}
                                    type={'number'} placeholder={'Quantity'}></Form.Control>
                            </Form.Group>
                            <Form.Group className="mb-2">
                                <Form.Control
                                    required
                                    value={product.description}
                                    onChange={e => updateInput(e)}
                                    name={'description'}
                                    as="textarea" rows={3} placeholder={'Description'}></Form.Control>
                            </Form.Group>
                            <Form.Group className="mb-2">
                                <Button variant={'success'} type={'submit'}>Upload Product</Button>
                                <Link to={'/products/fashion'} className="btn btn-dark ms-2">Cancel</Link>
                            </Form.Group>
                        </Form>
                    </Col>
                    <Col xs={3} className="mt-5">
                        <img src={product.imageUrl} alt="" className="img-fluid shadow-lg rounded-3" />
                    </Col>
                </Row>
            </Container>
        </>
    )
};
export default UploadProducts