import axios from "axios";
import { IProductRequest } from "../model/IProductRequest";
import { IProductResponse } from "../model/IProductResponse";

const serverUrl = process.env.REACT_APP_EXPRESS_SERVER_URL ? process.env.REACT_APP_EXPRESS_SERVER_URL : "";

/**
* @usage : Create a Product
* @url : http://localhost:9000/api/products/
* @body : title, description, imageUrl, brand, price, quantity, categoryId, subCategoryId
* @method : POST
* @access : PRIVATE
*/
export const createProduct = (product: IProductRequest): Promise<{ data: IProductResponse, msg: string }> => {
    const dataUrl: string = `${serverUrl}/api/products/`;
    return axios.post(dataUrl, product);
};

/**
* @usage : Update a Product
* @url : http://localhost:9000/api/products/:productId
* @body : title, description, imageUrl, brand, price, quantity, categoryId, subCategoryId
* @method : PUT
* @access : PRIVATE
*/
export const updateProduct = (product: IProductRequest, productId: string): Promise<{ data: IProductResponse, msg: string }> => {
    const dataUrl: string = `${serverUrl}/api/products/${productId}`;
    return axios.put(dataUrl, product);
};

/**
* @usage : Get all Products
* @url : http://localhost:9000/api/products/
* @body : no-params
* @method : GET
* @access : PRIVATE
*/
export const getAllProducts = (): Promise<{ data: IProductResponse[] }> => {
    const dataUrl: string = `${serverUrl}/api/products/`;
    return axios.get(dataUrl);
};

/**
* @usage : Get a Product
* @url : http://localhost:9000/api/products/:productId
* @body : no-params
* @method : GET
* @access : PRIVATE
*/
export const getProduct = (productId: string): Promise<{ data: IProductResponse }> => {
    const dataUrl: string = `${serverUrl}/api/products/${productId}`;
    return axios.get(dataUrl);
};

/**
* @usage : Delete a Product
* @url : http://localhost:9000/api/products/:productId
* @body : no-params
* @method : DELETE
* @access : PRIVATE
*/
export const deleteProduct = (productId: string): Promise<{ data: {}, msg: string }> => {
    const dataUrl: string = `${serverUrl}/api/products/${productId}`;
    return axios.delete(dataUrl);
};

/**
* @usage : Get all products with category Id
* @url : http://localhost:9000/api/products/categories/:categoryId
* @body : no-params
* @method : GET
* @access : PRIVATE
*/

export const getAllProductsWithCategoryId = (categoryId: string): Promise<{ data: IProductResponse[], msg: string }> => {
    const dataUrl: string = `${serverUrl}/api/products/categories/${categoryId}`;
    return axios.get(dataUrl);
};