import axios from "axios";
import { IUser } from "../model/IUser";

const serverUrl = process.env.REACT_APP_EXPRESS_SERVER_URL ? process.env.REACT_APP_EXPRESS_SERVER_URL : "";

/**
 * @usage : Register a User
 * @url : http://localhost:9000/api/users/register
 * @body : username , email , password
 * @method : POST
 * @access : PUBLIC
 */
export const registerUser = (user: IUser): Promise<{ data: IUser, msg: string }> => {
    const dataUrl: string = `${serverUrl}/api/users/register`;
    return axios.post(dataUrl, user);
};

/**
* @usage : Login a User
* @url : http://localhost:9000/api/users/login
* @body : email , password
* @method : POST
* @access : PUBLIC
*/
export const loginUser = (user: IUser): Promise<{ data: IUser, msg: string, token: string }> => {
    const dataUrl: string = `${serverUrl}/api/users/login`;
    return axios.post(dataUrl, user);
};

/**
*  @usage : get users Data
*  @url : http://localhost:9000/api/users/me
*  @method : GET
*  @param : no-params
*  @access : PRIVATE
*/
export const getUserData = (): Promise<{ data: IUser }> => {
    const dataUrl: string = `${serverUrl}/api/users/me`;
    return axios.get(dataUrl);
};

/**
* @usage : update profile Picture
* @url : http://localhost:9000/api/users/profile
* @body : imageUrl
* @method : POST
* @access : PRIVATE
*/
export const updateProfilePicture = (imageUrl: string): Promise<{ data: IUser, msg: string }> => {
    const dataUrl: string = `${serverUrl}/api/users/profile`;
    return axios.post(dataUrl, { imageUrl });
};

/**
* @usage : change the password
* @url : http://localhost:9000/api/users/change-password
* @body : password
* @method : POST
* @access : PRIVATE
*/
export const changePassword = (password: string): Promise<{ data: IUser, msg: string }> => {
    const dataUrl: string = `${serverUrl}/api/users/change-password`;
    return axios.post(dataUrl, { password });
};
