import { createAsyncThunk } from "@reduxjs/toolkit";
import * as OrderService from '../../module/orders/services/OrderService'
import * as AuthUtil from '../../util/AuthUtil'
import { IOrderRequest } from "../../module/orders/model/IOrderRequest";
import { IOrderResponse } from "../../module/orders/model/IOrderResponse";


export const placeOrderAction: any = createAsyncThunk("orders/placeOrderAction",
    async (payload: { order: IOrderRequest }, { rejectWithValue }): Promise<{ data: IOrderResponse, msg: string } | any> => {
        try {
            if (AuthUtil.isSetTokenToHeader()) {
                const { order } = payload;
                const response = await OrderService.placeOrder(order);
                return response.data;
            };
        } catch (error: any) {
            if (!error.response) {
                throw error
            };
            return rejectWithValue(error)
        };
    });

