import React, { useEffect } from 'react';
import './App.css';
import ToastConfig from './util/ToastConfig';
import { BrowserRouter, Route, Routes } from 'react-router-dom';
import Home from './module/ui/HomePage';
import FashionCatalogue from './module/products/pages/catalogues/fashion/FashionCatalogue';
import UserLogin from './module/user/pages/user-login/UserLogin';
import UserRegister from './module/user/pages/user-register/UserRegister';
import UserProfile from './module/user/pages/user-profile/UserProfile';
import ChangePassword from './module/user/pages/user-password/ChangePassword';
import MyOrders from './module/orders/pages/my-orders/MyOrders';
import CartPage from './module/carts/pages/cart-page/CartPage';
import ElectronicsCatalogue from './module/products/pages/catalogues/electronics/ElectronicsCatalogue';
import HouseHoldCatalogue from './module/products/pages/catalogues/household/HouseholdCatalogue';
import AddCategory from './module/categories/pages/add-sub-category/AddSubCategory';
import UploadProducts from './module/products/pages/upload-products/UploadProducts';
import ManageProducts from './module/products/pages/manage-products/ManageProducts';
import ManageOrders from './module/orders/pages/manage-orders/ManageOrders';
import { AppDispatch, useAppDispatch } from './redux/store';
import * as userActions from './redux/users/user.Actions'
import * as tokenUtil from './util/TokenUtil'
import AddShippingAddress from './module/user/pages/shipping-address/AddShippingAddress';
import EditShippingAddress from './module/user/pages/shipping-address/EditShippingAddress';
import ViewProduct from './module/products/pages/view-products/ViewProduct';
import CheckOutPage from './module/carts/pages/checkout-page/CheckOutPage';
import OrderDetails from './module/orders/pages/order-details/OrderDetails';
import EditProduct from './module/products/pages/edit-products/EditProduct';
import PrivateRoute from './router/PrivateRouter';
import AdminRoute from './router/AdminRouter';
import SuperAdminRoute from './router/SuperAdminRouter';


function App() {

    const dispatch: AppDispatch = useAppDispatch();

    useEffect(() => {
        if (tokenUtil.isLoggedIn()) {
            dispatch(userActions.getUserDataAction());
        }
    }, []);

    return (
        <div className="App">
            <ToastConfig />
            <BrowserRouter>
                <Routes>
                    <Route path='/' element={<Home />}></Route>

                    <Route path='/products/fashion' element={
                        <PrivateRoute><FashionCatalogue /></PrivateRoute>}>
                    </Route>

                    <Route path='/products/electronics' element={
                        <PrivateRoute><ElectronicsCatalogue /></PrivateRoute>}>
                    </Route>

                    <Route path='/products/household' element={
                        <PrivateRoute><HouseHoldCatalogue /></PrivateRoute>}>
                    </Route>

                    <Route path='/users/login' element={<UserLogin />}></Route>
                    <Route path='/users/register' element={<UserRegister />}></Route>

                    <Route path='/users/profile' element={
                        <PrivateRoute><UserProfile /></PrivateRoute>}>
                    </Route>

                    <Route path='/users/change-password' element={
                        <PrivateRoute><ChangePassword /></PrivateRoute>}>
                    </Route>

                    <Route path='/users/orders/me' element={
                        <PrivateRoute><MyOrders /></PrivateRoute>}>
                    </Route>
                    <Route path='/users/add-shipping-address' element={
                        <PrivateRoute><AddShippingAddress /></PrivateRoute>}>
                    </Route>

                    <Route path='/users/edit-shipping-address/:addressId' element={
                        <PrivateRoute><EditShippingAddress /></PrivateRoute>}>
                    </Route>

                    <Route path='/carts/list' element={
                        <PrivateRoute><CartPage /></PrivateRoute>}>
                    </Route>

                    <Route path='/carts/checkout' element={
                        <PrivateRoute><CheckOutPage /></PrivateRoute>}>
                    </Route>

                    <Route path='/categories/add' element={
                        <AdminRoute><AddCategory /></AdminRoute>}>
                    </Route>

                    <Route path='/products/upload' element={
                        <AdminRoute><UploadProducts /></AdminRoute>}>
                    </Route>

                    <Route path='/products/admin' element={
                        <SuperAdminRoute><ManageProducts /></SuperAdminRoute>}>
                    </Route>

                    <Route path='/products/view/:categoryName/:productId' element={
                        <PrivateRoute><ViewProduct /></PrivateRoute>}>
                    </Route>

                    <Route path='/products/edit/:productId' element={
                        <SuperAdminRoute><EditProduct /></SuperAdminRoute>}>
                    </Route>

                    <Route path='/orders/admin' element={
                        <SuperAdminRoute><ManageOrders /></SuperAdminRoute>}>
                    </Route>

                    <Route path='/orders/placed' element={
                        <PrivateRoute><OrderDetails /></PrivateRoute>}>
                    </Route>

                </Routes>
            </BrowserRouter>
        </div>
    );
}

export default App;
