import { createAsyncThunk } from "@reduxjs/toolkit";
import * as ProductService from '../../module/products/services/ProductService'
import * as AuthUtil from '../../util/AuthUtil'
import { IProductRequest } from "../../module/products/model/IProductRequest";
import { IProductResponse } from "../../module/products/model/IProductResponse";


export const createProductAction: any = createAsyncThunk("products/createProductAction",
    async (payload: { product: IProductRequest }, { rejectWithValue }): Promise<{ data: IProductResponse, msg: string } | any> => {
        try {
            if (AuthUtil.isSetTokenToHeader()) {
                const { product } = payload;
                const response = await ProductService.createProduct(product);
                return response.data;
            };
        } catch (error: any) {
            if (!error.response) {
                throw error
            };
            return rejectWithValue(error)
        };
    });

