
// this TokenUtil.ts is use to save token to localStorage.

// create TokenUtil.ts file

const SESSION_TOKEN_KEY: string = "contact-mgr-rct-jwt-token-key";

export const saveTokenToSession = (token: string): void => {
    sessionStorage.setItem(SESSION_TOKEN_KEY, token);
};

export const getToken = (): string | null => {
    return sessionStorage.getItem(SESSION_TOKEN_KEY);
};

export const isLoggedIn = (): boolean => {
    const token = getToken();
    if (token) {
        return true;
    } else {
        return false;
    }
};

export const deleteTokenFromSession = (): void => {
    return sessionStorage.removeItem(SESSION_TOKEN_KEY);
};
