
import { toast } from "react-toastify";

export const displaySuccessMessage = (msg: string): void => {
    toast.success(msg);
};
export const displayInfoMessage = (msg: string): void => {
    toast.info(msg);
};
export const displayWarnMessage = (msg: string): void => {
    toast.warn(msg);
};
export const displayErrorMessage = (msg: string): void => {
    toast.error(msg);
};
