import { ICartProduct, ICartResponse, ICartStorageType } from "../../module/carts/model/ICartResponse";
import { IProductResponse } from "../../module/products/model/IProductResponse";
import * as ToastUtil from '../../util/ToastUtil'

interface ProductPayload {
    product: IProductResponse;
    count: number;
};

const PRODUCT_TAX = 5.0;


export const convertFromServerToClientCart = (cart: ICartResponse) => {
    const products: ICartProduct[] = cart.products.map((product) => {
        return {
            _id: product?.productObj?._id,
            title: product?.productObj?.title,
            count: product?.count,
            description: product?.productObj?.description,
            imageUrl: product?.productObj?.imageUrl,
            brand: product?.productObj?.brand,
            price: product?.price,
            quantity: product?.productObj?.quantity,
            sold: product?.productObj?.sold,
            userObj: product?.productObj?.userObj,
            categoryObj: product?.productObj?.userObj,
            subCategoryObj: product?.productObj?.subCategoryObj,
            createdAt: product?.productObj?.createdAt,
            updatedAt: product?.productObj?.updatedAt,
        }
    });
    return {
        ...cart,
        products: products
    } as ICartStorageType;
};

// add to cart 
export const addToCartItem = (cart: ICartStorageType, payload: ProductPayload): ICartStorageType => {
    const { product, count } = payload;
    let newCart = cart;
    const isExists = cart.products.find(cartProduct => cartProduct._id === product._id);
    if (isExists) {
        ToastUtil.displayErrorMessage("Item already Exist");
    } else {
        newCart = {
            ...cart,
            products: [
                ...cart.products, {
                    ...product,
                    count: count,
                    userObj: product.userObj._id,
                    categoryObj: product.categoryObj._id,
                    subCategoryObj: product.subCategoryObj._id
                }]
        };
        ToastUtil.displaySuccessMessage("Item added to Cart");
    }
    return newCart;
};



//calculate total 
export const calculateTotal = (products: ICartProduct[]) => {
    let total: number = 0;
    for (let product of products) {
        total += Number(product.count) * Number(product.price);
    }
    return total;
};

//calculate tax
export const calculateTax = (products: ICartProduct[]) => {
    return calculateTotal(products) / 100 * PRODUCT_TAX;
};

//calculate grandTotal
export const calculateGrandTotal = (products: ICartProduct[]) => {
    return calculateTax(products) + calculateTotal(products);
};

// increment Qty 
export const incrementQtyOfCartItem = (cart: ICartStorageType, payload: { productId: string }) => {
    return {
        ...cart,
        products: cart.products.map(product => {
            if (product._id === payload.productId) {
                return {
                    ...product,
                    count: product.count + 1
                }
            }
            return product
        })
    };
};

// decrement qty 
export const decrementQtyOfCartItem = (cart: ICartStorageType, payload: { productId: string }): ICartStorageType => {
    return {
        ...cart,
        products: cart.products.map(product => {
            if (product._id === payload.productId) {
                return {
                    ...product,
                    count: product.count - 1 > 0 ? product.count - 1 : 1
                }
            }
            return product
        })
    };
};

// delete item 
export const deleteCartItem = (cart: ICartStorageType, payload: { productId: string }): ICartStorageType => {
    return {
        ...cart,
        products: cart.products.filter(product => product._id !== payload.productId)
    };
};
