import React, { useEffect, useState } from 'react'
import MainNavbar from '../../../ui/MainNavbar'
import LayoutHeading from '../../../ui/LayoutHeading'
import { Col, Container, Form, Row, Card, ListGroup, ListGroupItem } from "react-bootstrap";
import { Button } from "react-bootstrap";
import { ISubCategory } from '../../model/ISubCategory';
import * as categoryActions from '../../../../redux/categories/categories.Actions'
import * as categoryReducer from '../../../../redux/categories/categories.Reducer'
import { useSelector } from 'react-redux';
import { AppDispatch, RootState, useAppDispatch } from '../../../../redux/store';
import Spinner from '../../../../extraUtil/Spinner';
import { Link } from 'react-router-dom';
import { ICategory } from '../../model/ICategory';

const AddCategory: React.FC = () => {

    const dispatch: AppDispatch = useAppDispatch();

    const [validated, setValidated] = useState(false);  //form validation

    const [categoryId, setCategoryId] = useState<string>(""); //get category

    const [subCategory, setSubCategory] = useState<ISubCategory>({
        name: "",
        description: ""
    });

    const [subCategories, setSubCategories] = useState<ISubCategory[]>([] as ISubCategory[]); // get avlbl sub cat 

    const updateInput = (event: React.ChangeEvent<HTMLInputElement | any>) => {
        setSubCategory({
            ...subCategory,
            [event.target.name]: event.target.value
        })
    };

    // get all categories from redux
    const categoryReduxState: categoryReducer.InitialState = useSelector((state: RootState) => {
        return state[categoryReducer.categoryFeatureKey];
    });

    const { loading, categories } = categoryReduxState;

    useEffect(() => {
        dispatch(categoryActions.getAllCategoriesAction());
    }, []);

    const onSelectCategory = (e: React.ChangeEvent | any) => {
        setCategoryId(e.target.value);
        const category: ICategory | undefined = categories.find(category => category._id === e.target.value);
        if (category && category.subCategories) {
            setSubCategories(category.subCategories); // set sub-category state
        }
    };

    const handleSubmit = (event: React.FormEvent | any) => {
        event.preventDefault();
        const form = event.currentTarget;
        if (form.checkValidity() === true) {
            dispatch(categoryActions.createSubCategoryAction({ subCategory: subCategory, categoryId: categoryId })
            ).then((response: any) => {
                if (response && !response.error) {
                    dispatch(categoryActions.getAllCategoriesAction());
                    setCategoryId(""); // to make Empty state after submit form.
                    setSubCategory({  // to make Empty state after submit form.
                        name: "",
                        description: ""
                    })
                }
            });
        }
        if (form.checkValidity() === false) {
            event.preventDefault();
            event.stopPropagation();
        }
        setValidated(true);
    };

    return (
        <>
            {
                loading && <Spinner />
            }
            <MainNavbar />
            {/* 
      <pre>{JSON.stringify(categories)}</pre>
      <pre>{JSON.stringify(subCategory)}</pre>
      <pre>{JSON.stringify(categoryId)}</pre> */}

            <LayoutHeading icon='bi bi-ui-checks-grid' heading={'Add Sub-Category'} />

            {
                categories && categories.length > 0 &&
                <Container>
                    <Row>
                        <Col xs={4}>
                            <Form noValidate validated={validated} onSubmit={handleSubmit}>
                                <Form.Group className="mb-2">
                                    <Form.Select onChange={e => onSelectCategory(e)}>
                                        <option value="">Select a Category</option>
                                        {
                                            categories.map(category => {
                                                return (
                                                    <option value={category._id} key={category._id}>{category.name}</option>
                                                )
                                            })
                                        }
                                    </Form.Select>
                                </Form.Group>
                                <Form.Group className="mb-2">
                                    <Form.Control
                                        required
                                        value={subCategory.name}
                                        onChange={e => updateInput(e)}
                                        name={'name'}
                                        type={'text'} placeholder={'Subcategory Name'}></Form.Control>
                                </Form.Group>
                                <Form.Group className="mb-2">
                                    <Form.Control
                                        required
                                        value={subCategory.description}
                                        onChange={e => updateInput(e)}
                                        name={'description'}
                                        as="textarea" rows={3} placeholder={'Description'}></Form.Control>
                                </Form.Group>
                                <Form.Group className="mb-2">
                                    <Button variant={'success'} type={'submit'}>Create</Button>
                                    <Link to={'/products/fashion'} className="btn btn-dark ms-2">Cancel</Link>
                                </Form.Group>
                            </Form>
                        </Col>
                        <Col xs={4}>
                            <Card>
                                <Card.Header>
                                    <p className="h6">Available Sub-Categories</p>
                                </Card.Header>
                                <Card.Body>
                                    <ListGroup>
                                        {
                                            subCategories.map(subCategory => {
                                                return (
                                                    <ListGroupItem
                                                        key={subCategory._id}>{subCategory.name}</ListGroupItem>
                                                )
                                            })
                                        }
                                    </ListGroup>
                                </Card.Body>
                            </Card>
                        </Col>
                    </Row>
                </Container>
            }
        </>
    )
};
export default AddCategory