import React, { useState } from 'react'
import MainNavbar from '../../../ui/MainNavbar'
import LayoutHeading from '../../../ui/LayoutHeading'
import { Container, Form, Col, Row, Button } from 'react-bootstrap'
import { Link, useNavigate } from 'react-router-dom'
import { IUser } from '../../model/IUser'
import * as userActions from '../../../../redux/users/user.Actions'
import * as userReducer from '../../../../redux/users/user.Reducer'
import { AppDispatch, RootState, useAppDispatch } from "../../../../redux/store";
import { useSelector } from 'react-redux';
import Spinner from '../../../../extraUtil/Spinner';


const UserLogin: React.FC = () => {

    const dispatch: AppDispatch = useAppDispatch();
    const navigate = useNavigate();

    const [validated, setValidated] = useState(false);

    const [user, setUser] = useState<IUser>({
        email: "",
        password: ""
    });

    const updateInput = (event: React.ChangeEvent<HTMLInputElement | any>) => {
        setUser({
            ...user,
            [event.target.name]: event.target.value
        })
    };

    // get users data from store
    const userReduxState: userReducer.InitialState = useSelector((state: RootState) => {
        return state[userReducer.userFeatureKey];
    });

    const { loading } = userReduxState;

    const handleSubmit = (event: React.FormEvent | any) => {
        event.preventDefault();
        const form = event.currentTarget;
        if (form.checkValidity() === true) {
            dispatch(userActions.loginUserAction({ user: user })).then((response: any) => {
                if (response && !response.error) {
                    navigate("/products/fashion");
                }
            });
        }
        if (form.checkValidity() === false) {
            event.preventDefault();
            event.stopPropagation();
        }
        setValidated(true);
    };

    return (
        <>
            <MainNavbar />

            {
                loading && <Spinner />
            }

            {/* <pre>{JSON.stringify(user)}</pre> */}

            <LayoutHeading icon='bi bi-person-fill-lock'  heading={'Login User'} />
            <Container>
                <Row>
                    <Col xs={4}>
                        <Form noValidate validated={validated} onSubmit={handleSubmit}>
                            <Form.Group className="mb-2">
                                <Form.Control
                                    required
                                    name={'email'}
                                    value={user.email}
                                    onChange={e => updateInput(e)}
                                    type={'email'} placeholder={'Email'}></Form.Control>
                                <Form.Control.Feedback type="invalid">
                                    Please choose a valid Email.
                                </Form.Control.Feedback>
                            </Form.Group>
                            <Form.Group className="mb-2">
                                <Form.Control
                                    required
                                    pattern={"^(?!.*\\s)(?=.*[A-Z])(?=.*[a-z])(?=.*[0-9])(?=.*[~`!@#$%^&*]).{6,15}$"}
                                    name={'password'}
                                    value={user.password}
                                    onChange={e => updateInput(e)}
                                    type={'password'} placeholder={'Password'}></Form.Control>
                                <Form.Control.Feedback type="invalid">
                                    Please choose a Strong Password.
                                </Form.Control.Feedback>
                            </Form.Group>
                            <Form.Group className="mb-2">
                                <Button type={'submit'} variant={'success'}>Login</Button>
                                <Link to={'/'} className="btn btn-dark ms-2">Cancel</Link>
                            </Form.Group>
                        </Form>
                        <small>Don't have an account ?
                            <Link to={'/users/register'}
                                className="text-decoration-none fw-bold text-success"> Register</Link>
                        </small>
                    </Col>
                </Row>
            </Container>
        </>
    )
}
export default UserLogin