import axios from "axios";
import { ICartRequest } from "../model/ICartRequest";
import { ICartResponse } from "../model/ICartResponse";


const serverUrl = process.env.REACT_APP_EXPRESS_SERVER_URL ? process.env.REACT_APP_EXPRESS_SERVER_URL : "";

/**
 * @usage : create a Cart
 * @url : http://localhost:9000/api/carts/
 * @body :products[{product, count,price}],total,tax,grandTotal
 * @method : POST
 * @access : PRIVATE
 */
export const createCart = (cart: ICartRequest): Promise<{ data: ICartResponse, msg: string }> => {
    const dataUrl: string = `${serverUrl}/api/carts/`;
    return axios.post(dataUrl, cart);
};

/**
* @usage : get Cart Info
* @url : http://localhost:9000/api/carts/me
* @body : no-params
* @method : GET
* @access : PRIVATE
*/
export const getCartInfo = (): Promise<{ data: ICartResponse }> => {
    const dataUrl: string = `${serverUrl}/api/carts/me`;
    return axios.get(dataUrl);
};